.course-detail {
  padding-top: 0;
}

.course-header {
  background: linear-gradient(135deg, var(--primary-light) 0%, var(--white) 100%);
  padding: 60px 0;
}

.course-header-content {
  display: flex;
  align-items: center;
  gap: 40px;
}

.course-header-image {
  flex: 0 0 300px;
  height: 300px;
  background-color: var(--primary);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--box-shadow);
}

.course-header-image img {
  max-width: 80%;
  max-height: 80%;
  object-fit: contain;
}

.course-header-text {
  flex: 1;
}

.course-header-text h1 {
  font-size: 36px;
  margin-bottom: 15px;
  color: var(--primary);
}

.course-header-text p {
  font-size: 18px;
  color: var(--text-secondary);
  margin-bottom: 20px;
}

.course-meta-info {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.course-meta-item {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: var(--white);
  padding: 8px 15px;
  border-radius: 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.course-meta-item svg {
  width: 18px;
  height: 18px;
  color: var(--primary);
}

.course-price-block {
  background-color: var(--white);
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: var(--box-shadow);
  display: inline-block;
}

.course-price-label {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 5px;
}

.course-price-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--accent);
}

.course-actions {
  margin-top: 20px;
  display: flex;
  gap: 15px;
}

.back-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--primary);
  font-weight: 500;
  transition: all 0.3s ease;
  margin-bottom: 20px;
  text-decoration: none;
}

.back-button svg {
  width: 18px;
  height: 18px;
}

.back-button:hover {
  transform: translateX(-5px);
}

.course-section {
  padding: 60px 0;
  border-bottom: 1px solid var(--gray);
}

.course-section:last-child {
  border-bottom: none;
}

.course-section-title {
  font-size: 28px;
  margin-bottom: 30px;
  position: relative;
  padding-left: 15px;
}

.course-section-title::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background-color: var(--primary);
  border-radius: 3px;
}

.program-list {
  list-style: none;
  counter-reset: program-counter;
}

.program-item {
  position: relative;
  padding: 20px 20px 20px 70px;
  margin-bottom: 15px;
  background-color: var(--white);
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  counter-increment: program-counter;
}

.program-item::before {
  content: counter(program-counter);
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 36px;
  height: 36px;
  background-color: var(--primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
}

.program-item h3 {
  font-size: 18px;
  margin-bottom: 8px;
}

.program-item p {
  color: var(--text-secondary);
  font-size: 14px;
}

.teachers-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.teacher-card {
  background-color: var(--white);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: var(--box-shadow);
  transition: all 0.3s ease;
}

.teacher-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.teacher-image {
  height: 200px;
  background-color: var(--primary-light);
  display: flex;
  align-items: center;
  justify-content: center;
}

.teacher-image img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.teacher-info {
  padding: 20px;
  text-align: center;
}

.teacher-name {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 5px;
}

.teacher-position {
  color: var(--primary);
  font-weight: 500;
  margin-bottom: 10px;
}

.teacher-bio {
  color: var(--text-secondary);
  font-size: 14px;
}

.schedule-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.schedule-card {
  background-color: var(--white);
  border-radius: 10px;
  padding: 20px;
  box-shadow: var(--box-shadow);
}

.schedule-title {
  font-size: 18px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.schedule-title svg {
  width: 20px;
  height: 20px;
  color: var(--primary);
}

.schedule-list {
  list-style: none;
}

.schedule-item {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid var(--gray);
}

.schedule-item:last-child {
  border-bottom: none;
}

.schedule-day {
  font-weight: 500;
}

.schedule-time {
  color: var(--text-secondary);
}

.payment-options {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.payment-option {
  background-color: var(--white);
  border-radius: 10px;
  padding: 20px;
  box-shadow: var(--box-shadow);
  text-align: center;
  transition: all 0.3s ease;
}

.payment-option:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.payment-option.featured {
  border: 2px solid var(--accent);
  position: relative;
}

.payment-option.featured::before {
  content: 'Популярный';
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--accent);
  color: white;
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 700;
}

.payment-name {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
}

.payment-price {
  font-size: 24px;
  color: var(--accent);
  margin-bottom: 15px;
}

.payment-description {
  color: var(--text-secondary);
  font-size: 14px;
  margin-bottom: 20px;
}

.reviews-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

@media (max-width: 992px) {
  .course-header-content {
    flex-direction: column;
    text-align: center;
  }

  .course-meta-info {
    justify-content: center;
  }

  .course-actions {
    justify-content: center;
  }

  .teachers-grid,
  .payment-options,
  .reviews-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {

  .schedule-grid,
  .teachers-grid,
  .payment-options,
  .reviews-grid {
    grid-template-columns: 1fr;
  }
}
