/* Анимации для компонентов */

/* Плавные переходы для кнопок */
.btn {
  transition: all 0.3s ease !important;
}

.btn:hover {
  transform: translateY(-3px) !important;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15) !important;
}

.btn:active {
  transform: translateY(-1px) !important;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1) !important;
}

/* Анимации для карточек */
.feature,
.advantage,
.testimonial,
.course-card {
  transition: all 0.4s ease !important;
}

.feature:hover,
.advantage:hover,
.testimonial:hover,
.course-card:hover {
  transform: translateY(-10px) !important;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1) !important;
}

/* Анимация для шагов */
.step {
  position: relative;
  overflow: hidden;
}

.step::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 0;
  background-color: var(--primary);
  transition: height 0.8s ease;
}

.step:hover::before {
  height: 100%;
}

.step-number {
  transition: all 0.4s ease;
}

.step:hover .step-number {
  transform: scale(1.2);
  color: var(--accent);
}

/* Анимация для иконок */
.feature-icon,
.advantage-icon {
  transition: all 0.4s ease;
}

.feature:hover .feature-icon,
.advantage:hover .advantage-icon {
  transform: rotate(10deg) scale(1.1);
}

/* Анимация для FAQ */
.faq-item {
  transition: all 0.3s ease;
}

.faq-item:hover {
  background-color: rgba(0, 87, 255, 0.05);
}

.faq-icon {
  transition: transform 0.3s ease;
}

.faq-item.active .faq-icon {
  transform: rotate(180deg);
}

.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease;
}

.faq-item.active .faq-answer {
  max-height: 500px;
}

/* Анимация для формы */
.form-input {
  transition: all 0.3s ease;
  border: 1px solid #e0e0e0;
}

.form-input:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(0, 87, 255, 0.1);
}

/* Анимация для кнопки отправки формы */
.form-submit {
  position: relative;
  overflow: hidden;
}

.form-submit::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 5px;
  background: rgba(255, 255, 255, 0.3);
  opacity: 0;
  border-radius: 100%;
  transform: scale(1, 1) translate(-50%, -50%);
  transform-origin: 50% 50%;
}

.form-submit:hover::after {
  animation: ripple 1s ease-out;
}

@keyframes ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5;
  }

  20% {
    transform: scale(25, 25);
    opacity: 0.3;
  }

  100% {
    opacity: 0;
    transform: scale(40, 40);
  }
}

/* Анимация для логотипа */
.logo img {
  transition: transform 0.4s ease;
}

.logo:hover img {
  transform: rotate(10deg);
}

/* Анимация для навигации */
.nav-list a {
  position: relative;
  transition: color 0.3s ease;
}

.nav-list a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--primary);
  transition: width 0.3s ease;
}

.nav-list a:hover::after {
  width: 100%;
}

/* Анимация для социальных иконок */
.social-icons a {
  transition: all 0.3s ease;
}

.social-icons a:hover {
  transform: translateY(-5px) rotate(5deg);
}

/* Стили для карточек курсов */
.courses-section {
  padding: 80px 0;
  background-color: #f9f9ff;
}

.courses-container {
  display: flex;
  overflow-x: auto;
  padding: 20px 0;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: thin;
  scrollbar-color: var(--primary) #f1f1f1;
}

.courses-container::-webkit-scrollbar {
  height: 8px;
}

.courses-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.courses-container::-webkit-scrollbar-thumb {
  background-color: var(--primary);
  border-radius: 4px;
}

.course-card {
  flex: 0 0 300px;
  margin-right: 20px;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
}

.course-card:last-child {
  margin-right: 0;
}

.course-image {
  height: 160px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.course-image img {
  max-width: 100%;
  max-height: 120px;
  object-fit: contain;
}

.course-content {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.course-title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--primary);
}

.course-description {
  font-size: 14px;
  color: #666;
  margin-bottom: 15px;
  flex-grow: 1;
}

.course-meta {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  font-size: 14px;
}

.course-duration,
.course-level {
  display: flex;
  align-items: center;
}

.course-duration svg,
.course-level svg {
  width: 16px;
  height: 16px;
  margin-right: 5px;
}

.course-price {
  font-weight: 700;
  font-size: 18px;
  color: var(--accent);
  margin-bottom: 15px;
}

.course-actions {
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.course-button {
  width: 100%;
  padding: 10px;
  background-color: var(--primary);
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.course-button:hover {
  background-color: var(--accent);
}

.course-button.secondary {
  background-color: transparent;
  border: 1px solid var(--primary);
  color: var(--primary);
}

.course-button.secondary:hover {
  background-color: var(--primary);
  color: white;
}
