.chat-button-container {
  position: fixed;
  bottom: 30px;
  left: 30px;
  z-index: 1000;
}

.chat-button {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary);
  color: white;
  border: none;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.chat-button svg {
  width: 24px;
  height: 24px;
}

.chat-button:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.25);
}

.chat-button.active {
  background-color: var(--accent);
}

.chat-popup {
  position: absolute;
  bottom: 80px;
  left: 0;
  width: 320px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-header {
  background-color: var(--primary);
  color: white;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header h3 {
  margin: 0;
  font-size: 18px;
}

.close-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-button svg {
  width: 18px;
  height: 18px;
}

.chat-body {
  padding: 20px;
}

.chat-body .form-group {
  margin-bottom: 15px;
}

.chat-body label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
}

.chat-body .form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  font-family: inherit;
  font-size: 14px;
  transition: all 0.3s ease;
}

.chat-body .form-input:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 87, 255, 0.1);
}

.chat-submit {
  width: 100%;
  margin-top: 10px;
}

.chat-error {
  margin-top: 15px;
  padding: 10px;
  background-color: #ffebee;
  color: #c62828;
  border-radius: 5px;
  font-size: 14px;
  text-align: center;
}

.chat-success {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.chat-success svg {
  width: 50px;
  height: 50px;
  color: #4caf50;
  margin-bottom: 15px;
}

.chat-success p {
  margin-bottom: 20px;
  color: #333;
}

@media (max-width: 480px) {
  .chat-popup {
    width: 280px;
    left: -10px;
  }
  
  .chat-button-container {
    bottom: 20px;
    left: 20px;
  }
  
  .chat-button {
    width: 50px;
    height: 50px;
  }
}
